import * as React from 'react'
import { Link as RouterLink } from 'react-router-dom'
import * as Yup from 'yup'
import { Formik } from 'formik'
import EyeOutlined from '@ant-design/icons/EyeOutlined'
import EyeInvisibleOutlined from '@ant-design/icons/EyeInvisibleOutlined'
import {
  Button,
  Checkbox,
  Divider,
  FormControlLabel,
  FormHelperText,
  Grid,
  Link,
  InputAdornment,
  IconButton,
  InputLabel,
  OutlinedInput,
  Stack,
  Typography,
} from '@mui/material'
import Omniauth from '../shared/OmniAuth'

interface Prop {
  isDemo?: boolean
}
export default function AuthLogin({ isDemo = false }) {
  const [checked, setChecked] = React.useState(false)

  const [showPassword, setShowPassword] = React.useState(false)
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword)
  }

  const handleMouseDownPassword = (event) => {
    event.preventDefault()
  }

  const handleSubmit = (event) => {
    event.preventDefault()
  }

  return (
    <Formik
      onSubmit={handleSubmit}
      initialValues={{
        email: '',
        password: '',
        submit: null
      }}
      validationSchema={Yup.object().shape({
        email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
        password: Yup.string().max(255).required('Password is required')
      })}
    >
      {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
        <form noValidate onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <Stack spacing={1}>
                <InputLabel htmlFor='email'>Email Address</InputLabel>
                <OutlinedInput
                  id='email'
                  type='email'
                  value={values.email}
                  name='email'
                  onBlur={handleBlur}
                  onChange={handleChange}
                  placeholder='addo@yahoo.com'
                  fullWidth
                  error={Boolean(touched.email && errors.email)}
                />
              </Stack>
              {touched.email && errors.email && (
                <FormHelperText error id='standard-weight-helper-text-email'>
                  {/* {errors.email} */}
                </FormHelperText>
              )}
            </Grid>
            <Grid item xs={12}>
              <Stack spacing={1}>
                <InputLabel htmlFor='password'>Password</InputLabel>
                <OutlinedInput
                  fullWidth
                  error={Boolean(touched.password && errors.password)}
                  id='-password'
                  type={showPassword ? 'text' : 'password'}
                  value={values.password}
                  name='password'
                  onBlur={handleBlur}
                  onChange={handleChange}
                  endAdornment={
                    <InputAdornment position='end'>
                      <IconButton
                        aria-label='toggle password visibility'
                        onClick={handleClickShowPassword}
                        onMouseDown={handleMouseDownPassword}
                        edge='end'
                        color='secondary'
                      >
                        {showPassword ? <EyeOutlined /> : <EyeInvisibleOutlined />}
                      </IconButton>
                    </InputAdornment>
                  }
                  placeholder='Enter password'
                />
              </Stack>
              {touched.password && errors.password && (
                <FormHelperText error id='standard-weight-helper-text-password'>
                  {/* {errors.password} */}
                </FormHelperText>
              )}
            </Grid>

            <Grid item xs={12} sx={{ mt: -1 }}>
              <Stack direction='row' justifyContent='space-between' alignItems='center' spacing={2}>
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={checked}
                      onChange={(event) => setChecked(event.target.checked)}
                      name='checked'
                      color='primary'
                      size='small'
                    />
                  }
                  label={<Typography>Keep me sign in</Typography>}
                />
                <Typography component={RouterLink} to='/forgotPassword' variant='body2' sx={{ textDecoration: 'none' }} color='primary'>
                  Forgot Password?
                </Typography>
              </Stack>
            </Grid>
            {errors.submit && (
              <Grid item xs={12}>
                <FormHelperText error>
                  {/* {errors.submit} */}
                </FormHelperText>
              </Grid>
            )}
            <Grid item xs={12}>
              <Button disableElevation disabled={isSubmitting} fullWidth size='large' type='submit' variant='contained' color='primary'>
                Login
              </Button>
            </Grid>
            <Grid item xs={12}>
              <Divider>
                <Typography variant='caption'> Login with</Typography>
              </Divider>
            </Grid>
            <Grid item xs={12}>
              <Omniauth />
            </Grid>
          </Grid>
        </form>
      )}
    </Formik>
  )
}
